.wp-block-hsq-hsq-project-link::after {
  content: "";
  clear: both;
  display: table;
}

.wp-block-hsq-hsq-project-link > * {
  display: flex;
  clear: none;
  min-width: 150px;
}

.wp-block-hsq-hsq-project-link figure {
  width: 300px;
  min-width: 0;
  min-height: 80px;
  float: left;
  margin-right: 10px;
}